body {
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

#root {
  height: 100%;
}

.App {
  height: 100%;
}

.main {
  height: calc(100% - 64px);
}

.active-link {
  color: white;
  text-decoration: underline;
}

.nav-link {
  color: white;
}

.cart-link {
  color: white;
}
.error {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.error-text {
  margin-bottom: 30px !important;
}

.error-link {
  display: flex !important;
  align-items: center !important;
}

.error-icon {
  margin-right: 10px;
}
.product-list-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.product-list {
  margin: 30px auto;
  max-width: 1560px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

a {
  text-decoration: none;
}

.cart-table {
  margin: 30px auto;
  max-width: 700px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cart-header {
  text-align: center;
  margin: 30px 0 !important;
}

.total-price {
  margin: 40px;
}

